:root {
  --font-stack-serif: Constantia, Lucida Bright, Lucidabright, Georgia, serif;
  --font-stack-sansserif: Helvetica Neue, Helvetica, Arial, sans-serif;
  --font-stack-general: Helvetica Neue, Helvetica, Arial, sans-serif;
  --font-stack-heading: Helvetica Neue, Helvetica, Arial, sans-serif;
  --font-stack-monospace: Consolas, Ubuntu Mono, Lucida Console, Courier New, Courier, monospace;
  --font-size-base: 16px;
  --font-size-general: 1rem;
  --font-size-big: 1.125em;
  --font-size-xbig: 1.266em;
  --font-size-xxbig: 1.424em;
  --font-size-small: 0.889em;
  --font-size-xsmall: 0.79em;
  --font-size-xxsmall: 0.702em;
  --font-size-h1: 1.802em;
  --font-size-h2: 1.602em;
  --font-size-h3: 1.424em;
  --font-size-h4: 1.266em;
  --font-size-h5: 1.125em;
  --font-weight-normal: 400;
  --font-weight-bold: 700;
  --font-weight-heading: 700;
  --line-height-general: 1.5;
  --line-height-big: 1.45;
  --line-height-xbig: 1.4;
  --line-height-xxbig: 1.35;
  --line-height-small: 1.55;
  --line-height-xsmall: 1.6;
  --line-height-xxsmall: 1.65;
  --line-height-h1: 1.25;
  --line-height-h2: 1.3;
  --line-height-h3: 1.35;
  --line-height-h4: 1.4;
  --line-height-h5: 1.45;
  --line-height-general-in-rem: 1.5rem;
  --line-height-big-in-rem: 1.45rem;
  --line-height-xbig-in-rem: 1.4rem;
  --line-height-xxbig-in-rem: 1.35rem;
  --line-height-small-in-rem: 1.55rem;
  --line-height-xsmall-in-rem: 1.6rem;
  --line-height-xxsmall-in-rem: 1.65rem;
  --line-height-h1-in-rem: 1.25rem;
  --line-height-h2-in-rem: 1.3rem;
  --line-height-h3-in-rem: 1.35rem;
  --line-height-h4-in-rem: 1.4rem;
  --line-height-h5-in-rem: 1.45rem;
  --color-primary: #209ed5;
  --color-dark-accent: #002f5b;
  --color-light-accent: #e6f4f1;
  --color-background: #ffffff;
  --color-contrast: #252428;
  --color-contrast-medium: #8c8c8c;
  --color-contrast-low: #bfbfbf;
  --color-contrast-lower: #d9d9d9;
  --color-contrast-lowest: #f2f2f2;
  --color-contrast-high: #737373;
  --color-contrast-higher: #595959;
  --color-contrast-highest: #404040;
  --color-success: #27ae60;
  --color-alert: #c0392b;
  --color-warning: #d35400;
  --color-highlight: #f1c40f;
  --color-dribbble: #ea4c89;
  --color-facebook: #3b5998;
  --color-flickr: #ff0084;
  --color-instagram: #517fa4;
  --color-linkedin: #0077b5;
  --color-medium: #00ab6b;
  --color-pinterest: #bd081c;
  --color-rss: #f26522;
  --color-tumblr: #35465c;
  --color-twitter: #55acee;
  --color-youtube: #b31217;
  --space: 1rem;
  --space-top-h: calc(var(--space) * 1.25);
  --space-bottom-h: calc(var(--space) * 0.625);
  --alley: 30px;
  --p-inline-t: 0.125em;
  --p-inline-r: 0.25em;
  --p-inline-b: 0.125em;
  --p-inline-l: 0.25em;
  --padding-inline: var(--p-inline-t) var(--p-inline-r) var(--p-inline-b)
        var(--p-inline-l);
  --p-box-t: 0.75em;
  --p-box-r: 1.25em;
  --p-box-b: 0.75em;
  --p-box-l: 1rem;
  --padding-box: var(--p-box-t) var(--p-box-r) var(--p-box-b) var(--p-box-l);
  --p-message-t: 0.75em;
  --p-message-r: 3.25em;
  --p-message-b: 0.75em;
  --p-message-l: 1em;
  --padding-message: var(--p-message-t) var(--p-message-r) var(--p-message-b)
        var(--p-message-l);
  --p-table-t: 0.5em;
  --p-table-r: 2em;
  --p-table-b: 0.5em;
  --p-table-l: 0;
  --padding-table: var(--p-table-t) var(--p-table-r) var(--p-table-b)
        var(--p-table-l);
  --p-button-t: 0.375em;
  --p-button-r: 1em;
  --p-button-b: 0.375em;
  --p-button-l: 1em;
  --padding-button: var(--p-button-t) var(--p-button-r) var(--p-button-b)
        var(--p-button-l);
  --p-input-t: 0.375em;
  --p-input-r: 0.75em;
  --p-input-b: 0.375em;
  --p-input-l: 0.75em;
  --padding-input: var(--p-input-t) var(--p-input-r) var(--p-input-b)
        var(--p-input-l);
  --ease-in-out-expo: cubic-bezier(1, 0, 0, 1);
  --ease-in-out-quart: cubic-bezier(0.77, 0, 0.175, 1);
  --default-easing: cubic-bezier(1, 0, 0, 1);
  --border-size: 1px;
  --max-site-width: 1200px;
