html {
  overflow-y: scroll;
  box-sizing: border-box;
  font-size: var(--font-size-base);
  -ms-text-size-adjust: 100%;
}

html > div,
html > span {
  display: none;
}

body {
  margin: 0;
  padding: 0;
  background-color: var(--color-background);
  color: var(--color-contrast);
  word-wrap: break-word;
  font-weight: var(--font-weight-normal);
  font-family: var(--font-stack-general);
  line-height: var(--line-height-general);
  overflow-wrap: break-word;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

*,
*:before,
*:after {
  box-sizing: inherit;
  font-weight: inherit;
  font-size: inherit;
  font-family: inherit;
  line-height: inherit;
}

main,
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

p,
h1,
h2,
h3,
h4,
h5,
h6,
ol,
ul,
pre,
address,
blockquote,
dl,
div,
fieldset,
form,
hr,
noscript,
table,
main,
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
