  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: var(--font-weight-heading);
  font-family: var(--font-stack-heading);
}

h1 b,
h1 strong,
h2 b,
h2 strong,
h3 b,
h3 strong,
h4 b,
h4 strong,
h5 b,
h5 strong,
h6 b,
h6 strong {
  font-weight: var(--font-weight-heading);
}

ul {
  list-style: square;
}

ol {
  list-style: decimal;
}

ol,
ul {
  margin-left: calc(1em + 2px);
}

ol ol,
ol ul,
ul ol,
ul ul {
  margin-left: 1em;
}

dl dt {
  font-weight: var(--font-weight-bold);
}

dl dd {
  margin-left: calc(1em + 2px);
}

hr {
  height: var(--border-size);
  border: 0;
  background-color: var(--color-contrast-lower);
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: none;
}

a {
  color: var(--color-primary);
  text-decoration: none;
  transition: color 200ms;
}

a:hover {
  color: inherit;
}

input[type='submit']:not(:disabled), input[type='button']:not(:disabled), input[type='reset']:not(:disabled) {
  cursor: pointer;
}

textarea {
  resize: none;
}

button {
  overflow: visible;
  cursor: pointer;
}

fieldset {
  border: 0;
}

iframe {
  display: block;
}

iframe,
figure {
  max-width: 100%;
}

video {
  max-width: 100%;
  height: auto;
}

img {
  max-width: 100%;
  height: auto;
  border: 0;
}

img,
input,
textarea {
  vertical-align: top;
}

em,
dfn {
  font-style: italic;
}

b,
