}

table {
  width: 100%;
  border: none;
  border-spacing: 0;
  border-collapse: collapse;
  text-align: left;
}

table.fixed {
  table-layout: fixed;
}

caption {
  padding-top: calc(var(--space) * 0.375);
  border-top: var(--border-size) solid var(--color-contrast-lower);
  text-align: left;
  text-transform: uppercase;
  font-weight: var(--font-weight-bold);
  font-size: var(--font-size-xsmall);
}

tr,
th,
td {
  text-align: left;
}

tr {
  border-top: var(--border-size) solid var(--color-contrast-lower);
  border-bottom: var(--border-size) solid var(--color-contrast-lower);
  text-align: left;
}

thead,
tfoot,
th {
  font-weight: var(--font-weight-bold);
}

th,
td {
  padding: var(--padding-table);
}

figcaption {
  padding-top: calc(var(--space) * 0.375);
  color: var(--color-contrast-medium);
  text-align: center;
  font-size: var(--font-size-xsmall);
}

.group {
  max-width: 1200px;
  padding-right: 15px;
  padding-left: 15px;
}

@media (min-width: 480px) {
  .group {
    padding-right: 30px;
    padding-left: 30px;
  }
}

.table-container {
  overflow-x: auto;
}

.video-container {
  padding-bottom: 56.25%;
  position: relative;
  display: block;
  height: 0;
}

.video-container iframe,
.video-container object,
.video-container embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.screen-reader-text {
  position: absolute;
  overflow: hidden;
  clip: rect(1px, 1px, 1px, 1px);
  margin: -1px;
  padding: 0;
  width: 1px;
  height: 1px;
  border: 0;
  word-wrap: normal;
  clip-path: inset(50%);
}

