}

img.alignleft, img.alignright {
  padding-bottom: var(--space);
}

.alignleft,
.alignright {
  max-width: 50%;
}

blockquote.alignleft, blockquote.alignright {
  max-width: 40%;
}

.alignleft {
  float: left;
  margin-right: 1.25rem;
}

.alignright {
  float: right;
  margin-left: 1.25rem;
}

.aligncenter {
  display: block;
  margin-right: auto;
  margin-left: auto;
}

.size-auto, .size-full, .size-large, .size-medium, .size-thumbnail {
  max-width: 100%;
  height: auto;
}

.tiled-gallery,
.video-player {
  max-width: 100%;
