}

.tiled-gallery:not(:last-child),
.video-player:not(:last-child) {
  margin-bottom: var(--space) !important;
}

.gallery {
  margin-right: calc(calc(var(--alley) * -0.5) / 2);
  margin-left: calc(calc(var(--alley) * -0.5) / 2);
  display: flex;
  flex-wrap: wrap;
  margin-top: calc(var(--space) * -1);
  margin-bottom: var(--space);
}

.gallery > * {
  padding-right: calc(calc(var(--alley) * 0.5) / 2);
  padding-left: calc(calc(var(--alley) * 0.5) / 2);
}

.gallery > * {
  margin-top: var(--space);
}

.gallery-row {
  max-width: 100%;
}

.gallery-item {
  margin-bottom: 0;
  text-align: center;
}

.gallery-columns-1 > .gallery-item {
  width: 100%;
}

.gallery-columns-2 > .gallery-item {
  width: 50%;
}

.gallery-columns-3 > .gallery-item {
  width: 33.33333%;
}

